.header {
  position: relative;
  z-index: 5;
  &__registration,
  &__user {
    font-size: 14px;
    line-height: 130%;
    font-weight: 500;
    &:hover {
      color: #5856D2;
    }
  }
  .nice-select {
    width: auto;
    height: auto;
    background: transparent !important;
    border: none;
    padding: 0 18px 0 0;
    font-size: 14px;
    line-height: 130%;
    font-weight: 500;
    &:after {
      top: 50%;
      right: 0;
      transform: translate(0, -50%);
      width: 11px;
      height: 6px;
      background-image: url("../img/down-arrow.svg");
      background-repeat: no-repeat;
      background-position: center;
      background-size: auto;
    }
    .list {
      z-index: 3;
      width: auto;
      background-color: #fff;
      border: 1px solid #3F3F3F;
      li {
        background-color: transparent !important;
        border: none;
        padding: 4px 8px;
        font-size: 14px;
        line-height: 130%;
        &:not(:last-child) {
          border-bottom: 1px solid #3F3F3F;
        }
      }
      .selected {
        font-weight: 500;
      }
    }
  }
  .open {
    &:after {
      transform: translate(0, -50%) rotate(-180deg);
    }
  }
  .bg--active {
    opacity: 1;
    pointer-events: auto;
  }
  &__catalog {
    &-show {
      height: 50px;
      display: flex;
      justify-content: center;
      align-items: center;
      gap: 8px;
      background: #5856D2;
      border-radius: 12px 0 0 12px;
      padding: 5px 22px;
      cursor: pointer;
      transition: all 0.3s;
    }
    .catalog-burger {
      position: relative;
      width: 18px;
      height: 14px;
      flex-shrink: 0;
      span {
        position: absolute;
        left: 0;
        height: 2px;
        background: #fff;
        border-radius: 3px;
      }
      span:nth-child(1) {
        top: 0;
        width: 100%;
      }
      span:nth-child(2) {
        top: 4px;
        width: calc(100% - 4px);
      }
      span:nth-child(3) {
        bottom: 4px;
        width: 100%;
      }
      span:nth-child(4) {
        bottom: 0;
        width: calc(100% - 4px);
      }
    }
    .catalog-burger--active {
      span:nth-child(1) {
        transform: rotate(-45deg);
        top: 6px;
      }
      span:nth-child(2),
      span:nth-child(3) {
        display: none;
      }
      span:nth-child(4) {
        transform: rotate(45deg);
        bottom: 6px;
        width: 100%;
      }
    }
    .catalog-burger__text {
      font-size: 16px;
      line-height: 130%;
      font-weight: 600;
      color: #fff;
    }
    &-bg {
      position: fixed;
      z-index: 2;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, 0.5);
      opacity: 0;
      pointer-events: none;
      transition: all 0.3s;
    }
    &-hidden {
      position: absolute;
      z-index: 3;
      top: 100%;
      left: 50%;
      transform: translate(-50%, -10px);
      max-width: 1385px;
      width: 100%;
      max-height: 540px;
      background: #fff;
      border-radius: 0 0 12px 12px;
      padding: 30px;
      opacity: 0;
      pointer-events: none;
      transition: all 0.3s;
      overflow-y: auto;
      &::-webkit-scrollbar {
        width: 3px;
      }
      &::-webkit-scrollbar-track {
        background: #fff;
        border-radius: 3px;
      }
      &::-webkit-scrollbar-thumb {
        background: #5856D2;
        border-radius: 3px;
      }
      &--active {
        transform: translate(-50%, 0);
        opacity: 1;
        pointer-events: auto;
      }
    }
    &-wrap {
      display: flex;
      justify-content: space-between;
      align-items: stretch;
      gap: 50px;
    }
    &-head {
      width: 305px;
      max-height: 493px;
      overflow-y: auto;
      &::-webkit-scrollbar {
        width: 3px;
      }
      &::-webkit-scrollbar-track {
        background: #fff;
        border-radius: 3px;
      }
      &::-webkit-scrollbar-thumb {
        background: #5856D2;
        border-radius: 3px;
      }
    }
    &-body {
      width: calc(100% - 355px);
      max-height: 493px;
      overflow-y: auto;
      &::-webkit-scrollbar {
        width: 3px;
      }
      &::-webkit-scrollbar-track {
        background: #fff;
        border-radius: 3px;
      }
      &::-webkit-scrollbar-thumb {
        background: #5856D2;
        border-radius: 3px;
      }
    }
    &-tab,
    .item-show {
      display: flex;
      align-items: center;
      gap: 10px;
      cursor: pointer;
      transition: all 0.3s;
      i {
        width: 20px;
        flex-shrink: 0;
        display: flex;
        align-items: center;
        img {
          max-width: 100%;
        }
      }
      span {
        font-size: 16px;
        line-height: 130%;
        letter-spacing: 0.32px;
      }
    }
    .tab {
      border-radius: 8px;
      padding: 12px 14px;
      &:hover {
        background: #F8F8FC;
        span {
          font-weight: 500;
        }
      }
    }
    .tab--active {
      background: #F8F8FC;
      span {
        font-weight: 500;
      }
    }
    .tab__content {
      display: none;
      &--active {
        display: block;
      }
    }
    &-title {
      font-size: 24px;
      line-height: 130%;
      font-weight: 600;
      letter-spacing: 0.48px;
      span {
        font-size: 14px;
        line-height: 130%;
        color: #B2B2B2;
        margin-left: 12px;
      }
    }
    &-items {
      display: grid;
      grid-template-columns: repeat(3, calc(100% / 3 - 72px / 3));
      gap: 36px;
      margin-top: 24px;
    }
    &-item {
      &-subtitle {
        font-size: 16px;
        line-height: 130%;
        font-weight: 600;
        letter-spacing: 0.32px;
      }
    }
    &-list {
      display: grid;
      grid-template-columns: 100%;
      gap: 5px;
      margin-top: 10px;
      li a {
        font-size: 14px;
        line-height: 130%;
        color: #B2B2B2;
        &:hover {
          font-weight: 500;
          color: #3f3f3f;
        }
      }
    }
    .text-overflow {
      height: 134px;
      text-overflow: ellipsis;
      overflow: hidden;
    }
    .text-overflow--active {
      height: auto;
      text-overflow: unset;
      overflow: unset;
    }
    &-more {
      display: inline-block;
      cursor: pointer;
      margin-top: 10px;
      span {
        font-size: 14px;
        line-height: 130%;
        font-weight: 500;
        color: #5856D2;
      }
      span:nth-child(2) {
        display: none;
      }
    }
    .show-more--active {
      span:nth-child(1) {
        display: none;
      }
      span:nth-child(2) {
        display: block;
      }
    }
  }
  &__search {
    position: relative;
    input {
      width: auto;
      min-width: 200px;
      height: 50px;
      background: #fff;
      border: 1px solid #ECECEC;
      border-radius: 0 12px 12px 0;
      padding: 0 55px 0 25px;
      font-size: 16px;
      line-height: 130%;
      &::placeholder {
        color: #B2B2B2;
      }
    }
    &-btn {
      position: absolute;
      z-index: 2;
      top: 50%;
      right: 0;
      transform: translate(0, -50%);
      width: 50px;
      height: 50px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: transparent;
      border-left: 1px solid #ECECEC;
      padding: 5px;
      svg {
        max-width: 80%;
        max-height: 80%;
      }
    }
  }
  &__favourites,
  &__basket {
    display: flex;
    align-items: center;
    gap: 10px;
    &:hover {
      .header__favourites-text,
      .header__basket-text {
        color: #5856D2;
      }
    }
    &-icon {
      position: relative;
      width: 45px;
      height: 45px;
      flex-shrink: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #EDF1F7;
      border-radius: 50%;
    }
    &-number {
      position: absolute;
      top: -3px;
      right: -3px;
      width: 18.7px;
      height: 18.7px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #EA5252;
      border-radius: 50%;
      font-size: 9.6px;
      line-height: 130%;
      font-weight: 700;
      color: #fff;
    }
    &-text {
      font-size: 16px;
      line-height: 130%;
      font-weight: 600;
      transition: all 0.3s;
    }
  }
  .search-mob__btn {
    display: none;
  }
  .search-mob {
    display: none;
  }
}
.header-top {
  position: relative;
  z-index: 6;
  background: #F8F8FC;
  padding: 20px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 30px;
  }
  &__left {
    display: flex;
    align-items: center;
    gap: 30px;
  }
  &__list {
    display: flex;
    align-items: center;
    gap: 30px;
    li a {
      font-size: 14px;
      line-height: 160%;
      font-weight: 300;
      letter-spacing: 0.28px;
      &:hover {
        text-shadow: 0 0 .65px #3f3f3f, 0 0 .65px #3f3f3f;
      }
    }
  }
  &__btn {
    display: flex;
    align-items: center;
    gap: 9px;
    background: transparent;
    padding: 0;
    font-size: 14px;
    line-height: 130%;
    font-weight: 500;
    &:hover {
      color: #5856D2;
    }
    i {
      width: 29px;
      height: 29px;
      flex-shrink: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #5856D2;
      border-radius: 8.7px;
      img {
        max-width: 80%;
        max-height: 80%;
      }
    }
  }
  &__right {
    display: flex;
    align-items: center;
    gap: 30px;
  }
}
.header-bottom {
  position: relative;
  z-index: 5;
  background: #fff;
  padding: 20px 0;
  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 100px;
  }
  &__logo {
    width: 279px;
    display: block;
    img {
      width: 100%;
    }
  }
  &__right {
    flex-grow: 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 50px;
    .header-bottom__group {
      display: flex;
    }
  }
  &__group {
    flex-grow: 1;
    display: none;
    align-items: stretch;
  }
  .header__search {
    flex-grow: 1;
    input {
      width: 100%;
    }
  }
  &__group2 {
    display: flex;
    align-items: center;
    gap: 30px;
  }
}
.burger {
  width: 32px;
  display: none;
  img {
    width: 100%;
  }
}
.header-mob {
  position: fixed;
  z-index: 3;
  left: 0;
  bottom: 0;
  width: 100%;
  display: none;
  background: #fff;
  box-shadow: 0px -6px 40.8px 0px rgba(117, 117, 117, 0.25);
  border-radius: 10px 10px 0 0;
  padding: 15px 20px 10px;
  &__items {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    gap: 16px;
  }
  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    &-icon {
      position: relative;
      max-width: 24px;
      margin-bottom: auto;
      img {
        max-width: 100%;
      }
    }
    &-number {
      position: absolute;
      top: -4px;
      right: -4px;
      width: 14px;
      height: 14px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #EA5252;
      border-radius: 50%;
      font-size: 7px;
      line-height: 130%;
      font-weight: 700;
      color: #fff;
    }
    &-text {
      font-size: 12px;
      line-height: 130%;
      font-weight: 500;
      margin-top: 6px;
    }
  }
}

@media screen and (max-width: 1200px) {
  .header {
    &__catalog {
      &-hidden {
        max-height: 460px;
      }
      &-head {
        height: 400px;
      }
      &-body {
        height: 400px;
      }
    }
  }
  .header-bottom {
    &__wrap {
      gap: 30px;
    }
    &__logo {
      width: 220px;
    }
    &__right {
      gap: 30px;
    }
  }
}

@media screen and (max-width: 992px) {
  .header {
    &__favourites,
    &__basket {
      &-icon {
        position: relative;
        width: 35px;
        height: 35px;
        img {
          max-width: 14px;
        }
      }
      &-number {
        top: -2px;
        right: -2px;
        width: 15px;
        height: 15px;
        font-size: 7px;
      }
      &-text {
        font-size: 14px;
      }
    }
    &__catalog {
      &-bg {
        display: none;
      }
      &-hidden {
        position: fixed;
        top: 0;
        height: 100%;
        max-height: 100%;
        padding: 30px 30px 60px 30px;
      }
      &-top {
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: 16px;
      }
      &-close {
        width: 18px;
        cursor: pointer;
        svg {
          width: 100%;
          height: auto;
        }
      }
      .header__search {
        display: block !important;
        margin-top: 15px;
        input {
          border-radius: 12px;
        }
      }
      &-items2 {
        display: grid;
        grid-template-columns: 100%;
        gap: 20px;
        margin-top: 32px;
      }
      .item-show {
        position: relative;
        padding-right: 24px;
        &:after {
          content: '';
          position: absolute;
          top: 50%;
          right: 0;
          transform: translate(0, -50%);
          width: 16px;
          height: 8px;
          background-image: url("../img/down-arrow.svg");
          background-repeat: no-repeat;
          background-position: center;
          background-size: auto;
          transition: all 0.3s;
        }
      }
      .item-show--active {
        &:after {
          transform: translate(0, -50%) rotate(-90deg);
        }
      }
      .item-hidden {
        display: none;
        margin-top: 15px;
      }
      &-list {
        margin-top: 0;
      }
    }
  }
  .header-top {
    display: none;
  }
  .header-bottom {
    padding: 16px 0;
    &__wrap {
      gap: 24px;
    }
    &__right {
      flex-grow: unset;
      gap: 24px;
      .header-bottom__group {
        display: none;
      }
    }
    &__group {
      display: flex;
      margin-top: 16px;
    }
    &__group2 {
      gap: 24px;
    }
  }
  .burger {
    display: block;
  }
}

@media screen and (max-width: 767px) {
  .header-bottom {
    &__logo {
      width: 200px;
    }
  }
}

@media screen and (max-width: 576px) {
  .header {
    .search-mob__btn {
      display: block;
      i {
        width: 21px;
        display: flex;
        align-items: center;
        svg {
          max-width: 100%;
        }
      }
      i:nth-child(2) {
        display: none;
      }
      &--active {
        i:nth-child(1) {
          display: none;
        }
        i:nth-child(2) {
          display: flex;
        }
      }
    }
    .search-mob {
      margin-top: 10px;
      &--active {
        display: block;
      }
      .header__search {
        display: block;
        input {
          border-radius: 12px;
        }
      }
    }
  }
  .header-bottom {
    &__wrap {
      gap: 16px;
    }
    &__logo {
      width: 160px;
    }
    &__right {
      gap: 16px;
      .header-bottom__group {
        display: flex;
        margin-top: 0;
      }
    }
    &__wrap {
      .header__catalog {
        display: none;
      }
    }
    &__group {
      margin-top: 0;
    }
    .header__catalog-show {
      display: none;
    }
    &__group2 {
      display: none;
    }
    .header__search {
      display: none;
    }
  }
  .header-mob {
    display: block;
  }
}