.main {
  padding: 10px 0 50px;
  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    gap: 20px;
  }
  &__desc {
    position: relative;
    width: 55%;
    background: #F1E7EA;
    border-radius: 12px;
    padding: 50px 50px 60px 50px;
  }
  &__slider {
    position: relative;
    width: 45%;
    @media screen and (min-width: 768px) {
      .swiper-wrapper {
        transform: unset !important;
        display: grid;
        gap: 20px;
      }
      .swiper-slide {
        width: 100% !important;
      }
      .swiper-pagination {
        display: none;
      }
    }
    .swiper {
      height: 100%;
    }
    .swiper-wrapper {
      height: 100%;
      grid-template-columns: repeat(2, calc(50% - 20px / 2));
    }
  }
  &__title {
    position: relative;
    z-index: 2;
    max-width: 400px;
    font-size: 48px;
    line-height: 130%;
    font-weight: 700;
    span {
      color: #5856D2;
    }
  }
  &__items {
    position: relative;
    z-index: 2;
    max-width: 305px;
    display: grid;
    grid-template-columns: 100%;
    gap: 16px;
    margin-top: 16px;
  }
  &__item {
    display: flex;
    align-items: center;
    gap: 16px;
    &-icon {
      width: 30px;
      flex-shrink: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      img {
        max-width: 100%;
      }
    }
    &-text {
      font-size: 16px;
      line-height: 130%;
      span {
        display: block;
        font-weight: 600;
      }
    }
  }
  &__btn {
    position: relative;
    z-index: 2;
    height: 55px;
    display: inline-flex;
    padding: 5px 59px;
    margin-top: 16px;
    span {
      font-size: 16px;
    }
  }
  &__img {
    position: absolute;
    right: 16px;
    bottom: 0;
    width: 340px;
    img {
      width: 100%;
    }
  }
  &__slider2 {
    position: relative;
    margin-top: 20px;
    .swiper {
      overflow: unset;
    }
    .swiper-wrapper {
      min-width: 1385px;
      align-items: stretch;
    }
    .swiper-slide {
      width: auto !important;
      height: auto;
      flex-grow: 1;
    }
  }
}
.main-card {
  &__img {
    position: relative;
    height: 100%;
    display: block;
    border-radius: 12px;
    padding-bottom: 78%;
    overflow: hidden;
    &:hover {
      img {
        transform: scale(1.25);
      }
    }
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 12px;
      transition: all 0.5s;
    }
  }
  &__title {
    position: absolute;
    top: 0;
    left: 0;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    gap: 6px;
    background: #56BBD2;
    border-radius: 18px;
    padding: 9px 18px;
    margin: 15px;
    font-size: 11px;
    line-height: 130%;
    font-weight: 700;
    letter-spacing: 1.54px;
    color: #fff;
    i {
      width: 15px;
      flex-shrink: 0;
      img {
        width: 100%;
      }
    }
  }
}
.main-card2 {
  position: relative;
  max-width: 270px;
  min-height: 113px;
  display: flex;
  justify-content: space-between;
  align-items: stretch;
  border-radius: 12px;
  overflow: hidden;
  &--small {
    .main-card2__title {
      padding: 20px;
    }
    .main-card2__img {
      position: absolute;
      left: 50%;
      bottom: 0;
      transform: translate(-50%, 0);
      display: flex;
      justify-content: center;
    }
  }
  &--colour {
    background: #F2EFF9;
  }
  &--colour2 {
    background: #EFF3F9;
  }
  &--colour3 {
    background: #F9F6EF;
  }
  &--colour4 {
    background: #EFF9F2;
  }
  &--colour5 {
    background: #F9EFEF;
  }
  &--colour6 {
    background: #EFF3F9;
  }
  &:last-child {
    margin-right: 0 !important;
  }
  &__title {
    position: relative;
    z-index: 2;
    order: -1;
    padding: 20px 5px 20px 20px;
    font-size: 18px;
    line-height: 130%;
    font-weight: 500;
  }
  &__img {
    max-width: 100px;
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    img {
      //max-width: 100%;
      //max-height: 100%;
    }
    img:nth-child(2) {
      display: none;
    }
  }
  .top-img {
    top: 0;
    bottom: unset;
    display: flex;
    justify-content: flex-end;
    align-items: start;
  }
  .img-end {
    justify-content: flex-end !important;
    align-items: flex-end !important;
  }
}
.events {
  padding: 50px 0;
  &__cards {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 60px / 4));
    gap: 20px;
    margin-top: 30px;
  }
}
.event-card {
  position: relative;
  &:hover {
    &:after {
      opacity: 1;
    }
    .event-card__link {
      display: flex;
    }
  }
  &:before {
    content: '';
    position: absolute;
    z-index: 1;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 50%;
    background: linear-gradient(0.00deg, rgb(63, 63, 63),rgba(63, 63, 63, 0) 100%);
    border-radius: 0 0 12px 12px;
    pointer-events: none;
  }
  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(88, 86, 210, 0.7);
    border-radius: 12px;
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s;
  }
  &__img {
    position: relative;
    display: block;
    border-radius: 12px;
    padding-bottom: 62%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 12px;
    }
  }
  &__desc {
    position: absolute;
    z-index: 2;
    left: 0;
    bottom: 0;
    padding: 20px 25px;
  }
  &__title {
    font-size: 24px;
    line-height: 130%;
    font-weight: 500;
    color: #fff;
  }
  &__link {
    display: none;
    align-items: center;
    gap: 12px;
    margin-top: 8px;
    &:hover {
      span {
        text-decoration-color: #fff;
      }
    }
    span {
      font-size: 14px;
      line-height: 130%;
      font-weight: 600;
      text-decoration: underline;
      text-decoration-color: transparent;
      color: #fff;
      transition: all 0.3s;
    }
    i {
      width: 14px;
      flex-shrink: 0;
      img {
        width: 100%;
      }
    }
  }
}
.works {
  padding: 50px 0 100px;
  &2 {
    padding: 100px 0 50px;
  }
  &__slider {
    position: relative;
    margin-top: 30px;
  }
  &__slider-mob {
    display: none;
    margin-top: 30px;
  }
  @media screen and (min-width: 768px) {
    .works-swiper {
      overflow: unset;
    }
    .works-wrapper {
      transform: unset !important;
      display: grid;
      gap: 40px 20px;
      .swiper-slide {
        width: 100% !important;
      }
      .swiper-pagination {
        display: none;
      }
    }
    .swiper-pagination {
      display: none;
    }
  }
  &-wrapper {
    grid-template-columns: repeat(4, calc(25% - 60px / 4));
  }
}
.work-card {
  position: relative;
  &:hover {
    z-index: 4;
    &:after {
      opacity: 1;
    }
    .work-card__img {
      img {
        border-radius: 12px 12px 0 0;
      }
    }
    .work-card__desc {
      padding-left: 20px;
      padding-right: 20px;
    }
    .work-card__btn {
      opacity: 1;
      pointer-events: auto;
    }
  }
  &:after {
    content: '';
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    width: 100%;
    height: calc(100% + 75px);
    background: #fff;
    box-shadow: 0px 0px 37px 0px rgba(191, 191, 191, 0.22);
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s;
  }
  &__top {
    position: relative;
    z-index: 3;
    .work-card__favorite {
      display: none;
    }
  }
  &__slider {
    position: relative;
  }
  .swiper-slide {
    position: relative;
  }
  .swiper-button-prev,
  .swiper-button-next {
    opacity: 1 !important;
    &:after {
      font-size: 30px;
      color: #3f3f3f;
    }
  }
  .swiper-pagination {
    bottom: -16px !important;
    display: block !important;
    &-bullet-active {
      background: #5856D2;
    }
  }
  &__img {
    position: relative;
    display: block;
    padding-bottom: 100%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 12px;
    }
  }
  &__group {
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 5px;
    padding: 12px;
  }
  .discount,
  .delivery,
  .hit,
  .professional,
  .sale {
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 18px;
    padding: 5px 7px;
    font-size: 10.79px;
    line-height: 127%;
    letter-spacing: 0.2px;
    color: #fff;
  }
  .discount {
    background: #D25683;
  }
  .delivery {
    background: #55B070;
  }
  .hit {
    background: #DBAE1C;
  }
  .professional {
    background: #5856D2;
  }
  .sale {
    background: #EA5252;
  }
  &__increase {
    position: absolute;
    z-index: 2;
    right: 15px;
    bottom: 15px;
    width: 38px;
    height: 38px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgba(63, 63, 63, 0.5);
    border-radius: 6px;
    pointer-events: none;
    img {
      max-width: 18px;
    }
  }
  &__desc {
    position: relative;
    z-index: 2;
    padding: 25px 0 15px 0;
  }
  &__title {
    min-height: 52px;
    font-size: 20px;
    line-height: 130%;
    font-weight: 600;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp:2;
    -webkit-box-orient: vertical;
  }
  &__group2 {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 10px;
    margin-top: 12px;
  }
  &__price-wrap {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 5px 10px;
  }
  &__price-news {
    font-size: 24px;
    line-height: 130%;
    font-weight: 600;
    color: #5856D2;
    span {
      font-weight: 400;
    }
  }
  &__price-old {
    font-size: 16px;
    line-height: 130%;
    text-decoration: line-through;
    color: rgba(63, 63, 63, 0.5);
  }
  &__discount {
    font-size: 16px;
    line-height: 130%;
    font-weight: 500;
    color: #EA5252;
  }
  &__favorite {
    width: 24px;
    flex-shrink: 0;
    cursor: pointer;
    &--active {
      svg path {
        fill: #5856D2;
      }
    }
    svg {
      max-width: 100px;
      width: auto;
      height: auto;
      path {
        transition: all 0.3s;
      }
    }
  }
  &__feedback {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 10px;
    margin-top: 12px;
    &-text {
      font-size: 14px;
      line-height: 130%;
      font-weight: 500;
      text-decoration: underline;
      &:hover {
        text-decoration: unset;
      }
    }
    &-star {
      display: flex;
      align-items: center;
      gap: 7px;
      i {
        width: 17px;
        flex-shrink: 0;
        img {
          width: 100%;
        }
      }
      span {
        font-size: 15.6px;
        line-height: 130%;
        font-weight: 700;
      }
    }
  }
  &__type {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    background: #F3F3FF;
    border-radius: 18px;
    padding: 7px 9.5px;
    font-size: 11px;
    line-height: 130%;
    letter-spacing: 0.22px;
    color: #5856D2;
    margin-top: 16px;
  }
  &__btn {
    position: absolute;
    z-index: 2;
    left: 50%;
    bottom: -55px;
    transform: translate(-50%, 0);
    width: calc(100% - 40px);
    height: 55px;
    gap: 10px;
    padding: 5px;
    opacity: 0;
    &:hover {
      opacity: 0.8 !important;
    }
    i {
      width: 18px;
      flex-shrink: 0;
      img {
        width: 100%;
      }
    }
    span {
      font-size: 16px;
    }
  }
}
